















import { Component, Prop, Vue, Emit } from "vue-property-decorator";

@Component
export default class Prisons extends Vue {
  rooms: Array<string> = [
    "empty",
    "altar_level_1",
    "altar_level_2",
    "altar_level_3",
    "altar_level_4",
    "old_prison",
    "prison",
    "iron_prison",
    "magic_prison",
    "workhouse",
    "prison_factory",
    "power_station",
    "prisoner_and_guard",
    "prisoner_lab",
    "giant_laboratory",
    "howling_prison",
    "execution_ground",
    "bloodcry"
  ];

  get slicedRooms(): Array<Array<string>> {
    return this.rooms.reduce(
      (rows: Array<Array<string>>, item) => {
        const lastRow = rows[rows.length - 1];
        if (lastRow.length === 5) {
          rows.push([item]);
          return rows;
        } else {
          lastRow.push(item);
          return rows;
        }
      },
      [[]]
    );
  }

  @Emit("set-room")
  setRoom(room: string) {}

  onClick(room: string) {
    this.setRoom(room);
  }
}
